@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&family=Roboto:wght@400;500&display=swap');

* {
  font-family: Poppins;
}

.register-main {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  padding: 20px;
}

.card-main {
  height: 85vh;
  background: lightblue;
  position: relative;
  
  border-radius: 10px;
  -webkit-box-shadow: -1px 0px 5px 3px rgba(189, 189, 189, 1);
  -moz-box-shadow: -1px 0px 5px 3px rgba(189, 189, 189, 1);
  box-shadow: -1px 0px 5px 3px rgba(189, 189, 189, 1);
  
}

.inputs-container{
  display: flex;
  flex-direction: column;
  width: 400px;
  position: absolute;
  left: 50%;
  top: 43%;
  transform: translate(-50%, -35%);
}

.input-fields{
  margin: 5px;
  height: 30px;
  border-radius: 20px 20px 20px 20px;
  padding: 10px;
  border: 1px solid #bdbdbd;
  outline: none;
}

.input-fields:focus{
  border: 2px solid #ffc107;
}

.input-btn{
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  border: 1px solid #bdbdbd;
  border-radius: 20px 20px 20px 20px;
  background-color: #7f9600;
  color: white;
  cursor: pointer;
}
.input-btn:hover{
  background-color: black;
  border-radius: 20px 20px 20px 20px;
}
.home-M{
text-align: center;
padding :30px;
}
.pass{
  position: absolute;
  margin-top: 15px;
  right: 30px;
  border-radius: 15%;
}
.pass-M{
  position: relative;
  top:70px;
  
  padding:0 30px;
  
  
  
}
.home-id{
  display: flex;
  gap:20px;
  background-color:whitesmoke;
  border-radius: 20px 20px 20px 20px;
  padding : 0px 20px;
  margin-top: 10px;
  justify-content: space-between;
 
 
}
.passData{
  font-size: 14px;
}
.eye{
  margin: 10px;
  margin-right: 1px;
  cursor: pointer;
}
.passModal{
  display: flex;
  flex-direction: column;
  
}   